          b-btn.mt-3(type='reset' variant='danger') ÈáçÁΩÆ
          b-btn.mt-3.ml-3(variant='success' @click='checkout' :disabled='products.length === 0') ÁµêÂ∏≥
</template>

<script>
export default {
  data () {
    return {
      products: [],
      fields: [
        { key: 'image', label: 'ÂúñÁâá' },
        { key: 'name', label: 'ÂêçÁ®±' },
        { key: 'price', label: 'ÂÉπÊ†º' },
        { key: 'action', label: 'Êï∏Èáè' }
      ],
      options1: [
        { text: 'ÂÆÖÈÖç', value: 'ÂÆÖÈÖç' },
        { text: 'Ëá™Âèñ', value: 'Ëá™Âèñ' }
      ],
      form: {
        name: '',
        phone: '',
        address: '',
        courier: '',
        deliveryDate: '',
        deliveryTime: '',
        total: '',
        image: null,
        remark: ''
      }
    }
  },
  methods: {
    async updateCart (index, quantity) {
      try {
        await this.api.patch('/users/me/cart',
          { product: this.products[index].product._id, quantity },
          {
            headers: {
              authorization: 'Bearer ' + this.user.token
            }
          }
        )
        if (quantity === 0) {
          this.products.splice(index, 1)
          // Ë≥ºÁâ©ËªäÂà™Èô§È†ÖÁõÆ Âè≥‰∏äÊï∏ÁõÆÊõ¥Êîπ
          this.$store.commit('user/updateCart', this.user.cart - 1)
        }
      } catch (error) {
        console.log(error)
        this.$swal({
          icon: 'error',
          title: 'Â§±Êïó',
          text: '‰øÆÊîπË≥ºÁâ©ËªäÂ§±Êïó'
        })
      }
    },
    async checkout () {
      const fd = new FormData()
      for (const key in this.form) {
        if (key !== '_id') {
          /* Êääthis.formÂ°ûÈÄ≤ formdata */
          fd.append(key, this.form[key])
        }
        console.log(fd)
      }
      try {
        await this.api.post('/orders', fd, {
          headers: {
            authorization: 'Bearer ' + this.user.token
          }
        })
        this.$router.push('/orders')
        // this.$router.push('/back/member/memberinfo')
        // ÁµêÂ∏≥ÂÆåË≥ºÁâ©ËªäÊï∏ÈáèÊ≠∏Èõ∂
        this.$store.commit('user/updateCart', 0)
        this.$swal({
          icon: 'success',
          title: 'ÊàêÂäü',
          text: 'ÈÄÅÂá∫Ë®ÇÂñÆÔºåÂ∞à‰∫∫Â∞áÊñº‰∏äÁè≠Êó•ÂÖ©Â§©ÂÖßËàáÊÇ®ËÅØÁµ°'
        })
      } catch (error) {
        console.log(error)
        this.$swal({
          icon: 'error',
          title: 'Â§±Êïó',
          text: 'ÁµêÂ∏≥Â§±Êïó'
        })
      }
    },
    // Â∑≤‰∏ãÊû∂ÁöÑÂïÜÂìÅÊúÉÂá∫ÁèæÁ¥ÖÂ∫ï
    rowClass (item, type) {
      if (!item || type !== 'row') return
      return !item.product.sell ? 'bg-danger' : ''
    },
    onSubmit (event) {
      event.preventDefault()
      alert(JSON.stringify(this.form))
    },
    onReset (event) {
      event.preventDefault()
      // Reset our form values
      this.form.name = ''
      this.form.phone = ''
      this.form.address = ''
      this.form.courier = ''
      this.form.remark = ''
      // Trick to reset/clear native browser form validation state
      this.show = false
      this.$nextTick(() => {
        this.show = true
      })
    }
  },
  computed: {
    validation () {
      return {
        name: this.form.name.length >= 1,
        phone: this.form.phone.length === 10
      }
    },
    state () {
      return Boolean(this.form.courier)
    },
    total () {
      return this.products.reduce((accumulator, currentValue) => {
        return accumulator + currentValue.quantity * currentValue.product.price
      }, 0)
    }
  },
  async created () {
    try {
      const { data } = await this.api.get('/users/me/cart', {
        headers: {
          authorization: 'Bearer ' + this.user.token
        }
      })
      this.products = data.result
    } catch (error) {
      this.$swal({
        icon: 'error',
        title: 'Â§±Êïó',
        text: 'ÂèñÂæóË≥ºÁâ©ËªäÂ§±Êïó'
      })
    }
  }
}
</script>
