              b-form-group(label='ÂØÜÁ¢º' label-for='input-account' description='Â∏≥ËôüÈï∑Â∫¶ÁÇ∫ 4 Âà∞ 20 ÂÄãÂ≠ó' :state='state.account' invalid-feedback='Â∏≥ËôüÊ†ºÂºè‰∏çÊ≠£Á¢∫')
