<script>
export default {
  data () {
    return {
      form: {
        account: '',
        password: ''
      },
      btnFlag: false
    }
  },
  // ÂõûÂà∞È†ÇÈÉ®Ë®≠ÂÆö
  mounted () {
    window.addEventListener('scroll', this.scrollToTop)
  },
  destroyed () {
    window.removeEventListener('scroll', this.scrollToTop)
  },
  methods: {
    login () {
      this.$refs['my-modal'].hide()
      this.$store.dispatch('user/login', this.form)
    },
    logout () {
      this.$store.dispatch('user/logout')
    },
    hideModal () {
      this.form.account = ''
      this.form.password = ''
    },
    random (R) {
      return Math.floor(Math.random() * R)
    },
    async signForLine () {
      let link = 'https://access.line.me/oauth2/v2.1/authorize?'
      link += 'response_type=code' // ‰ΩøÁî®ËÄÖÁôªÂÖ•ÂæåÔºåË´ãLINEÂõûÂÇ≥„Äåcode„ÄçÔºàÊéàÊ¨äÁ¢ºÔºâ
      link += '&client_id=' + process.env.VUE_APP_CHANNEL_ID
      link += '&redirect_uri=' + process.env.VUE_APP_CALLBACK_URL // /users/signInLine
      link += '&state=' + this.randomState
      // TODO: state Âª∫Ë≠∞Âú® Web app Ë´ãÊ±Ç‰∏≠ÔºåÈáùÂ∞çÊØèÂÄãÁôªÂÖ•ÈöéÊÆµÈö®Ê©üÁîüÊàê„ÄÇ‰∏¶Á¢∫Ë™çË©≤ÂÄºËàáWeb app ‰∏≠Êé•Êî∂ÊéàÊ¨äÁ¢ºÊôÇÁöÑ state Â±¨ÊÄßÂÄº‰∏ÄËá¥„ÄÇ
      link += '&bot_prompt=normal' // È†êË®≠Ë¶ÅÂä†ÂÆòÊñπÂ∏≥ËôüÂ•ΩÂèã
      link += '&scope=openid%20profile' // È†êË®≠Áî≥Ë´ã‰ΩøÁî®ËÄÖË≥áÊñôÂèä token
      window.location.href = link
      // window.open(link, '_self') // Ë∑≥ËΩâÈ†ÅÈù¢

      // Âú® LINE Login ÊéàÊ¨ä URL Ôºå‰∏¶ËÆìÁî®Êà∂ÈáçÊñ∞Â∞éÂêë
      // https://access.line.me/oauth2/v2.1/authorize?response_type=code&client_id={CHANNEL_ID}&redirect_uri={CALLBACK_URL}&state={STATE}&bot_prompt={BOT_PROMPT}&scope={SCOPE_LIST}

      // ÂõûÂÇ≥ÁöÑ  https://dtns-test-app.herokuapp.com/auth?friendship_status_changed=false&code=JFJstzoT7w62112rXfyy&state=MX44ZkxPWUg%3D
    },
    // ÂõûÂà∞È†ÇÈÉ®
    backTop () {
      const that = this
      const timer = setInterval(() => {
        const ispeed = Math.floor(-that.scrollTop / 5)
        document.documentElement.scrollTop = document.body.scrollTop = that.scrollTop + ispeed
        if (that.scrollTop === 0) {
          clearInterval(timer)
        }
      }, 16)
    },

    // È´òÂ∫¶Â§ßÊñº50 Âá∫Áèæicon
    scrollToTop () {
      const that = this
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      that.scrollTop = scrollTop
      if (that.scrollTop > 50) {
        that.btnFlag = true
      } else {
        that.btnFlag = false
      }
    }
  },
  computed: {
    state () {
      return {
        account: this.form.account.length === 0 ? null : this.form.account.length >= 4,
        password: this.form.password.length === 0 ? null : this.form.password.length >= 4
      }
    }
  },
  async created () {
    this.$store.dispatch('user/getInfo')
    this.$store.dispatch('user/signInLine')
  }
}
</script>
